import React, { useEffect } from 'react';
import { useStyles } from './TShirtTemplate.style';
import product from "assets/icons/editor/product.png";
import circle from "assets/icons/editor/circle.png";
import { Typography } from '@material-ui/core';
import { Add, Check } from '@material-ui/icons';

interface PropsTypes {
    title: string;
    subtitle: string;
    size: string;
    colorCount: number;
    onClick: () => void;
    selected: boolean;
    imageUrl: string;
}

export const TShirtTemplate: React.FC<PropsTypes> = ({ selected, title, subtitle, size, colorCount, onClick, imageUrl }) => {

    const c = useStyles()

    useEffect(() => {
    }, [selected])

    return (
        <div className={c.container}>
            <div className={c.leftPart}>
                <img src={imageUrl} />
            </div>
            <div className={c.rightPart}>
                <Typography className={c.header}>{title}</Typography>
                <Typography className={c.simpleTxt}>{subtitle}</Typography>
                <div className={c.iconCont}>
                    <img src={product} />
                    <Typography className={c.simpleTxt}>Size {size}</Typography>
                </div>
                <div className={c.iconCont}>
                    <img src={circle} />
                    <Typography className={c.simpleTxt}>{colorCount} Colors</Typography>
                </div>
                {
                    selected
                        ?
                        <div className={c.check} onClick={onClick}>
                            <Check className={c.checkIcon} />
                        </div>
                        :
                        <div className={c.add} onClick={onClick}    >
                            <Add className={c.addIcon} />
                        </div>
                }
            </div>
        </div>
    )
}