import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles((theme) => ({
    container: {
        border: "1px solid #D5DAEA",
        borderRadius: 11,
        width: 480,
        height: 240,
        display: "flex",
        marginBottom: 40
    },
    leftPart: {
        borderTopLeftRadius: 11,
        borderBottomLeftRadius: 11,
        width: "50%",
        height: "100%",
        backgroundColor: "#F1F4F8",
        display: "flex",
        alignItems: "center",
        justifyContent: 'center',
        "& img": {
            width: "80%"
        }
    },
    rightPart: {
        padding: 30,
        position: "relative",
        height: "100%"
    },
    header: {
        fontSize: 18,
        fontWeight: 600,
        marginBottom: 10
    },
    simpleTxt: {
        fontSize: 14,
        marginBottom: 10
    },
    iconCont: {
        display: `flex`,
        marginBottom: 10,
        alignItems: 'center',
        '& p': {
            margin: 0,
            marginLeft: 10
        }
    },
    check: {
        position: "absolute",
        right: 40,
        bottom: 40,
        width: 30,
        height: 30,
        backgroundColor: theme.palette.primary.main,
        borderRadius: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center"
    },
    add: {
        position: "absolute",
        right: 40,
        bottom: 40,
        width: 30,
        height: 30,
        border: "2px solid #E8EDF4",
        borderRadius: "100%",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        "&:hover": {
            backgroundColor: "#E8EDF4",
            cursor: 'pointer'
        },
        "&:hover svg": {
            color: "white"
        }
    },
    checkIcon: {
        color: "white",
        fontSize: 23
    },
    addIcon: {
        color: "#E8EDF4",
        fontSize: 23
    },
}))