import { Grid, Tab, Tabs, Typography } from "@material-ui/core";
import { BonfireTabPanel } from "components/BonfireTabPanel";
import React, { useEffect, useState } from "react";
import { useStyles } from './campaignDesign.style';
import { TShirtTemplate } from "./components/tShirtTemplate";
import shirt from "assets/icons/editor/shirt.png";
import shirt1 from "assets/icons/shirt.png";


export const CampaignDesign = () => {
    const [tabValue, setTabValue] = useState(0)
    const [selectedIds, setSelectedIds] = useState<number[]>([])
    const c = useStyles();


    const handleSelecting = (id) => {
        if (selectedIds.includes(id)) {
            setSelectedIds(selectedIds.filter((e) => e !== id));
        }
        else {
            if (selectedIds.length < 9) {
                setSelectedIds([...selectedIds, id])
            }
        }
    }

    function a11yProps(index) {
        return {
            id: `simple-tab-${index}`,
            'aria-controls': `simple-tabpanel-${index}`,
        };
    }

    const apiData = [
        {
            id: 1,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt
        },
        {
            id: 2,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt
        },
        {
            id: 3,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt
        },
        {
            id: 4,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt
        },
        {
            id: 5,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt1
        },
        {
            id: 6,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt1
        },
        {
            id: 7,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt1
        },
        {
            id: 8,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt1
        },
        {
            id: 9,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt1
        },
        {
            id: 10,
            title: "Premium Unisex Tee",
            subtitle: "Our most popular shirt. Classic cut, soft ringspun fabric.",
            size: "S - 2XL",
            colorCount: 27,
            imgUrl: shirt1
        }
    ]

    useEffect(() => {
    }, [selectedIds])

    return (
        <div className={c.pageContainer}>
            <Typography className={c.header}>Select  up 9 styles</Typography>
            <div>
                <Tabs className={c.tabs} value={tabValue} onChange={(_, value) => setTabValue(value)} aria-label="simple tabs example">
                    <Tab label={<p className={c.tabName}>shirts</p>} {...a11yProps(0)} />
                    <Tab label={<p className={c.tabName}>face masks</p>} {...a11yProps(1)} />
                    <Tab label={<p className={c.tabName}>Tote bags</p>} {...a11yProps(2)} />
                    <Tab label={<p className={c.tabName}>Mugs</p>} {...a11yProps(3)} />
                </Tabs>
            </div>
            <BonfireTabPanel value={tabValue} index={0}>
                <Grid container xs={12} className={c.tShirtCont}>
                    <Grid item container lg={9}>
                        {
                            apiData.map(({ id, title, subtitle, size, colorCount, imgUrl }) => (
                                <Grid item xs={6}>
                                    <TShirtTemplate
                                        key={id}
                                        selected={selectedIds.includes(id)}
                                        title={title}
                                        subtitle={subtitle}
                                        size={size}
                                        colorCount={colorCount}
                                        onClick={() => handleSelecting(id)}
                                        imageUrl={imgUrl}
                                    />
                                </Grid>
                            ))
                        }
                    </Grid>
                    <div className={c.rightModal}>
                        <Typography className={c.modalTitle}>
                            SELECTED  PRODUCTS
                        </Typography>
                        <div style={{ height: 300, overflowY: 'scroll' }}>
                            {
                                apiData.filter((item) => selectedIds.includes(item.id)).map(({ id, title, imgUrl }) => (
                                    <div key={id} className={c.selectItem}>
                                        <img src={imgUrl} />
                                        <Typography>{title}</Typography>
                                    </div>
                                ))
                            }
                        </div>
                        <button
                            style={{ width: "100%", marginTop: 50 }}
                            className="btn-primary btn-sm"
                            onClick={() => { }} >
                            save products
                         </button>
                    </div>
                </Grid>
            </BonfireTabPanel>
            <BonfireTabPanel value={tabValue} index={1}>
                salam abi2
            </BonfireTabPanel>
            <BonfireTabPanel value={tabValue} index={2}>
                salam abi3
            </BonfireTabPanel>
            <BonfireTabPanel value={tabValue} index={3}>
                salam ab5
            </BonfireTabPanel>
        </div >
    )
}